/* card-icon */
.card-icon {
  color: #4154f1;
  background: #f6f6fe;
}
.card-icon {
  font-size: 32px;
  line-height: 0;
  width: 64px;
  height: 64px;
  flex-shrink: 0;
  flex-grow: 0;
}



